export default {
  name: 'DepositTermsModal',

  props: {
    visible: {
      type: Boolean,
      required: true,
    },
  },

  data: () => ({
    loading: false,
    isDisabled: true,
  }),

  methods: {
    onChange(checkedValues) {
      if (checkedValues.length === 6) {
        this.isDisabled = false
        this.$emit('accepted', true)
      } else {
        this.isDisabled = true
        this.$emit('accepted', false)
      }
    },

    closeModal() {
      // this.$emit('closeDepositTermsModal')
    },

    agree() {
      if (!this.isDisabled) {
        this.$emit('approved')
      }
    },
  },
}
