export default {
  name: 'BranchInfo',

  data: () => ({
    isMobile: null,
  }),

  props: {
    title: {
      type: String,
      required: true,
    },
    explanation: {
      type: String,
      default: 'Unavailable',
    }
  },

  mounted() {
    this.isMobile = window.innerWidth < 1000

    window.addEventListener('resize', () => {
      this.isMobile = window.innerWidth < 1000
    })
  },

  methods: {
    handleSuccess(e) {
    },

    handleError(e) {
    }
  }
}
